@font-face {
  font-family: 'Vogue';
  src: url('./assets/fonts/Vogue.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
}

.App {
  width: 100vw;
  margin: 0;
  padding: 0;
}

/* Seção principal com a imagem de overlay */
.main-section {
  position: relative;
  background-color: #f6d7f3;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.image-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
}

.image-overlay img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 1;
}

/* Header fica sticky por conta do próprio CSS */
.icon-header {
  position: sticky;
  top: 0;
  background-color: white;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column; /* Centraliza ícones e barra verticalmente */
  z-index: 1000;
  padding: 0;
  transition: background-color 0.3s ease-in-out, height 0.3s ease-in-out;
}

.icon-container {
  display: flex;
  gap: 40px;
}

.icon-button {
  background: none;
  border: none;
  font-size: 30px;
  cursor: pointer;
  color: black;
}

/* Barra preta abaixo dos ícones */
.icon-header::after {
  content: '';
  display: block;
  width: 40%; /* Tamanho ajustado da barra */
  height: 1px;
  background-color: black;
  margin-top: 10px;
  z-index: 2;
}

.sticky .icon-header {
  background-color: white;
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.sticky .icon-button {
  color: black;
}

.sticky .icon-header::after {
  background-color: black;
  margin-bottom: 0px;
  width: 40%;
}

.centered-text h1 {
  font-size: 100px;
  color: rgb(0, 0, 0);
  font-family: 'Vogue', sans-serif;
  text-shadow: 2px 4px 6px rgba(0, 0, 0, 0.3);
}

.centered-text {
  position: relative;
  top: 15%;
  left: 0%;
  transform: translate(0%, -50%);
  text-align: center;
  z-index: 2;
}

/* Galeria */
.gallery {
  position: absolute;
  top: 0%;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  z-index: 1;
}

/* Nova seção em branco */
/* Estilo da segunda parte (com a imagem de fundo e overlay) */
.blank-section {
  position: relative;
  background-image: url('./images/Overlay\ 2.png'); /* Substitua pelo caminho correto da sua imagem */
  background-size: cover;
  background-position: center;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
  overflow: hidden; /* Garante que o conteúdo não exceda a tela */
}

/* Overlay que ficará por cima da imagem */
.blank-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0); /* Cor do overlay com 50% de opacidade */
  z-index: 2;
}

/* Conteúdo dentro da segunda parte */
.blank-section-content {
  position: relative;
  z-index: 3; /* Garante que o conteúdo fique acima do overlay */
  color: white;
  font-size: 48px;
}

