.gallery {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  width: 60%;
  transform: translate(-83.5%, 0%); /* Mantém a centralização */
  margin: 0 auto;
}

.carousel {
  display: flex;
  animation: scroll-carousel 40s linear infinite;
}

.carousel img {
  width: calc(25% - 20px);
  height: 500px;
  margin-right: 20px;
  object-fit: cover;
  flex-shrink: 0;
}

/* Efeito de fade-out nas bordas do carrossel */
.gallery::before,
.gallery::after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  width: 20%;
  pointer-events: none;
  z-index: 2;
  filter: blur(6px);
}

.gallery::before {
  left: 0;
  background: linear-gradient(to right, rgba(255, 143, 221, 0.5), rgba(255, 255, 255, 0) 60%);
}

.gallery::after {
  right: 0;
  background: linear-gradient(to left, rgba(255, 255, 255, 0.6), rgba(255, 255, 255, 0) 60%);
}

@keyframes scroll-carousel {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}
