@font-face {
  font-family: 'Vogue';
  src: url('./assets/fonts/Vogue.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
}

/* Aplicando a fonte Vogue para toda a página */
* {
  font-family: 'Vogue', sans-serif;
}

.music-player-page {
  display: flex;
  height: 100vh;
  background-image: url('./images/Underlay.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  flex-direction: column;
}

@media (min-width: 768px) {
  .music-player-page {
    flex-direction: row;
  }
}

.left-menu {
  flex-basis: 35%;
  background-color: #f6d7d700;
  padding: 20px;
  z-index: 1;
}

@media (max-width: 768px) {
  .left-menu {
    flex-basis: 80%;
  }
}

.left-menu ul {
  list-style: none;
  padding-left: 100px;
  padding-top: 200px;
}

.left-menu .title {
  font-size: 32px;
  font-weight: bold;
  margin-bottom: 25px;
}

.left-menu li {
  font-size: 50px;
  margin-bottom: 20px;
  cursor: pointer;
  text-shadow: 2px 4px 6px rgba(0, 0, 0, 0.3);
}

.left-menu li.active {
  font-weight: bold;
  background-color: #f3b0c2;
  color: #000;
  border-radius: 5px;
  padding: 5px;
}

.main-content {
  flex-basis: 70%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

@media (max-width: 768px) {
  .main-content {
    flex-basis: 100%;
    padding: 10px;
  }
}

.vinyl-container {
  position: relative;
  width: 100%;
  height: 100%;
}

.vinyl-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
}

.vinyl-record {
  position: absolute;
  right: 30%;
  width: 700px;
  height: 700px;
  z-index: 2;
  transform: translateY(-45%);
}

@media (max-width: 768px) {
  .vinyl-record {
    width: 250px;
    height: 250px;
    right: 5%;
  }
}

.vinyl-record img {
  width: 200%;
  height: 200%;
  border-radius: 50%;
  overflow: hidden;
}

.vinyl-record.spin img {
  animation: spin 4s linear infinite;
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.play-pause-button {
  position: absolute;
  top: 5%;
  left: 5%;
  background-color: #f3b0c2;
  color: #000;
  border: none;
  border-radius: 20%;
  padding: 12px 24px;
  font-size: 16px;
  cursor: pointer;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  transition: background-color 0.3s, transform 0.2s;
  z-index: 3;
}

.play-pause-button:hover {
  background-color: #f3b0c2;
  transform: scale(1.05);
}

.play-pause-button:active {
  transform: scale(0.95);
}

.navigation-arrows {
  position: absolute;
  bottom: 10%;
  left: 5%;
  display: flex;
  gap: 15px;
  flex-direction: column;
  z-index: 3;
}

.navigation-arrows button {
  background-color: #f3b0c2;
  color: #000;
  border: none;
  border-radius: 20%;
  padding: 10px 20px;
  font-size: 18px;
  cursor: pointer;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  transition: background-color 0.3s, transform 0.2s;
}

.navigation-arrows button:hover {
  background-color: #f3b0c2;
  transform: scale(1.10);
}

.navigation-arrows button:active {
  transform: scale(0.95);
}

@media (max-width: 768px) {
  .play-pause-button, .navigation-arrows button {
    font-size: 18px;
    padding: 8px;
  }
}

/* Ajustes específicos para iPad de 10ª geração em paisagem */
@media (min-width: 768px) and (max-width: 1620px) and (orientation: landscape) {
  .left-menu .title {
    font-size: 28px;
  }

  .left-menu li {
    font-size: 40px;
  }

  .left-menu ul {
    padding-left: 40px;
    padding-top: 150px;
  }

  .play-pause-button {
    font-size: 14px;
  }

  .navigation-arrows button {
    font-size: 16px;
  }

  .vinyl-record {
    width: 500px;
    height: 500px;
    right: 10%;
  }
}

/* Classes para as animações de transição do vinyl-wrapper */
.vinyl-enter {
  transform: translateX(100%);
  opacity: 0;
}

.vinyl-enter-active {
  transform: translateX(0);
  opacity: 1;
  transition: transform 500ms ease-in-out, opacity 500ms ease-in-out;
}

.vinyl-exit {
  transform: translateX(0);
  opacity: 1;
}

.vinyl-exit-active {
  transform: translateX(100%);
  opacity: 0;
  transition: transform 500ms ease-in-out, opacity 500ms ease-in-out;
}
