/* BlankSection.css */
.blank-section {
  width: 100%;
  min-height: 300px; /* Defina conforme necessário */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}


/* iPad 10ª geração */
@media (min-width: 820px) and (max-width: 824px) {
  .blank-section {
    background-image: url('./images/Overlay\ 2_iPad.png');
  }
}
